
import { useState } from 'react'
import { Employee } from '@/types/purchase'
import { formatDate } from '@/utils/helpers'
import { Button } from '@/components/ui/button'
import { 
  Table, 
  TableBody, 
  TableCell, 
  TableHead, 
  TableHeader, 
  TableRow 
} from '@/components/ui/table'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { 
  MoreHorizontal, 
  Edit, 
  Trash2, 
  Eye, 
  ToggleLeft, 
  ToggleRight,
  Shield
} from 'lucide-react'
import { employeeService } from '@/services/employeeService'
import { toast } from 'sonner'
import { useTableSort } from '@/hooks/useTableSort'
import { SortableHeader } from '@/components/ui/sortable-header'
import { MobileCard, MobileCardItem, MobileCardHeader, MobileCardActions } from '@/components/ui/mobile-card'

interface EmployeeTableProps {
  employees: Employee[]
  onEdit: (employee: Employee) => void
  onView: (employee: Employee) => void
  onRefresh: () => void
  currentUserRoles?: string[]
}

export default function EmployeeTable({ employees, onEdit, onView, onRefresh, currentUserRoles = [] }: EmployeeTableProps) {
  const [loadingId, setLoadingId] = useState<string | null>(null)
  const { sortedData, sortConfig, handleSort } = useTableSort(employees, 'name', 'asc')
  const canViewAllColumns = currentUserRoles.includes('app_admin') || currentUserRoles.includes('hr')

  const handleToggleStatus = async (employee: Employee) => {
    if (!canViewAllColumns) {
      toast.error('   .')
      return
    }
    setLoadingId(employee.id)
    try {
      const result = await employeeService.toggleEmployeeStatus(employee.id)
      
      if (result.success) {
        toast.success(` ${employee.is_active ? '' : ''}.`)
        onRefresh()
      } else {
        toast.error(result.error || '  .')
      }
    } catch (error) {
      toast.error('    .')
    } finally {
      setLoadingId(null)
    }
  }

  const handleDelete = async (employee: Employee) => {
    if (!canViewAllColumns) {
      toast.error('  .')
      return
    }
    if (!confirm(` '${employee.name}'  ?`)) {
      return
    }

    setLoadingId(employee.id)
    try {
      const result = await employeeService.deleteEmployee(employee.id)
      
      if (result.success) {
        toast.success(' .')
        onRefresh()
      } else {
        toast.error(result.error || ' .')
      }
    } catch (error) {
      toast.error('   .')
    } finally {
      setLoadingId(null)
    }
  }

  const normalizeRoles = (role?: string | string[] | null) => {
    if (!role) return []
    if (Array.isArray(role)) {
      return role.filter((value) => value && value.trim())
    }
    if (typeof role === 'string') {
      return role.split(',').map((value) => value.trim()).filter(Boolean)
    }
    return []
  }

  const getPrimaryRole = (role?: string | string[] | null) => {
    const roles = normalizeRoles(role)
    return roles[0]
  }

  const getRoleDisplayName = (role?: string | string[] | null) => {
    const roleNames: Record<string, string> = {
      'app_admin': ' ',
      'ceo': 'CEO',
      'final_approver': ' ',
      'middle_manager': ' ',
      'lead buyer': ' ',
      'buyer': ''
    }
    
    const primaryRole = getPrimaryRole(role)
    if (!primaryRole) {
      return ' '
    }
    return roleNames[primaryRole] || primaryRole
  }

  const getRoleBadgeColor = (role?: string | string[] | null) => {
    const colorMap: Record<string, string> = {
      'app_admin': 'bg-purple-100 text-purple-800',
      'ceo': 'bg-red-100 text-red-800',
      'final_approver': 'bg-hansl-100 text-hansl-800',
      'middle_manager': 'bg-green-100 text-green-800',
      'lead buyer': 'bg-yellow-100 text-yellow-800',
      'buyer': 'bg-gray-100 text-gray-800'
    }
    
    const primaryRole = getPrimaryRole(role)
    return colorMap[primaryRole || ''] || 'bg-gray-100 text-gray-600'
  }

  // formatDate utils/helpers.ts import

  return (
    <>
      {/* Desktop Table View */}
      <div className="hidden lg:block border rounded-lg overflow-hidden">
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead className="w-16 min-w-[50px]">
              <SortableHeader
                sortKey="employeeID"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('employeeID' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-14 min-w-[45px]">
              <SortableHeader
                sortKey="name"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('name' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-12 min-w-[40px]">
              <SortableHeader
                sortKey="position"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('position' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-16 min-w-[50px]">
              <SortableHeader
                sortKey="department"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('department' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-20 min-w-[70px]">
              <SortableHeader
                sortKey="phone"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('phone' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-32 min-w-[100px]">
              <SortableHeader
                sortKey="email"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('email' as keyof Employee)}
              >
                
              </SortableHeader>
            </TableHead>
            {canViewAllColumns && (
              <>
                {/*   */}
                <TableHead className="w-11 min-w-[40px] text-center"></TableHead>
                <TableHead className="w-11 min-w-[40px] text-center"></TableHead>
                <TableHead className="w-11 min-w-[40px] text-center"></TableHead>
                {/* lg    */}
                <TableHead className="hidden lg:table-cell w-18 min-w-[60px]">
                  <SortableHeader
                    sortKey="join_date"
                    currentSortKey={sortConfig.key as string | null}
                    sortDirection={sortConfig.direction}
                    onSort={() => handleSort('join_date' as keyof Employee)}
                  >
                    
                  </SortableHeader>
                </TableHead>
                <TableHead className="hidden lg:table-cell w-20 min-w-[70px]">
                  <SortableHeader
                    sortKey="birthday"
                    currentSortKey={sortConfig.key as string | null}
                    sortDirection={sortConfig.direction}
                    onSort={() => handleSort('birthday' as keyof Employee)}
                  >
                    
                  </SortableHeader>
                </TableHead>
                {/*   */}
                <TableHead className="hidden xl:table-cell w-14 min-w-[45px]"></TableHead>
                <TableHead className="hidden xl:table-cell w-24 min-w-[80px]"></TableHead>
                <TableHead className="hidden 2xl:table-cell min-w-[120px]"></TableHead>
                <TableHead>
                  <SortableHeader
                    sortKey="purchase_role"
                    currentSortKey={sortConfig.key as string | null}
                    sortDirection={sortConfig.direction}
                    onSort={() => handleSort('purchase_role' as keyof Employee)}
                  >
                    
                  </SortableHeader>
                </TableHead>
                <TableHead>
                  <SortableHeader
                    sortKey="is_active"
                    currentSortKey={sortConfig.key as string | null}
                    sortDirection={sortConfig.direction}
                    onSort={() => handleSort('is_active' as keyof Employee)}
                  >
                    
                  </SortableHeader>
                </TableHead>
                <TableHead className="w-16 min-w-[50px]"></TableHead>
              </>
            )}
              </TableRow>
            </TableHeader>
        <TableBody>
          {employees.length === 0 ? (
            <TableRow>
              <TableCell colSpan={canViewAllColumns ? 14 : 6} className="text-center py-8 text-gray-500">
                  .
              </TableCell>
            </TableRow>
          ) : (
            sortedData.map((employee) => (
              <TableRow key={employee.id}>
                <TableCell className="text-[11px] px-2 py-1.5">
                  {employee.employeeID || employee.employee_number || employee.id.slice(0, 8)}
                </TableCell>
                <TableCell className="text-[11px] px-2 py-1.5">
                  {employee.name}
                </TableCell>
                <TableCell className="text-[11px] px-2 py-1.5">{employee.position || '-'}</TableCell>
                <TableCell className="text-[11px] px-2 py-1.5">{employee.department || '-'}</TableCell>
                <TableCell className="text-[11px] px-2 py-1.5">{employee.phone || '-'}</TableCell>
                <TableCell className="text-[11px] px-2 py-1.5">{employee.email || '-'}</TableCell>
                {canViewAllColumns && (
                  <>
                    {/*   */}
                    <TableCell className="text-center text-[11px] px-1 py-1.5">
                      {employee.annual_leave_granted_current_year || 0}
                    </TableCell>
                    <TableCell className="text-center text-[11px] px-1 py-1.5">
                      {employee.used_annual_leave || 0}
                    </TableCell>
                    <TableCell className="text-center text-[11px] px-1 py-1.5">
                      {employee.remaining_annual_leave !== undefined 
                        ? employee.remaining_annual_leave
                        : '-'}
                    </TableCell>
                    {/* lg    */}
                    <TableCell className="hidden lg:table-cell text-[11px] px-2 py-1.5">
                      {formatDate(employee.join_date)}
                    </TableCell>
                    <TableCell className="hidden lg:table-cell text-[11px] px-2 py-1.5">
                      {formatDate(employee.birthday)}
                    </TableCell>
                    {/*   */}
                    <TableCell className="hidden xl:table-cell text-[11px] px-2 py-1.5">{employee.bank || '-'}</TableCell>
                    <TableCell className="hidden xl:table-cell text-[11px] px-2 py-1.5">{employee.bank_account || '-'}</TableCell>
                    <TableCell className="hidden 2xl:table-cell text-[11px] px-2 py-1.5">{employee.adress || '-'}</TableCell>
                    <TableCell className="px-2 py-1.5">
                      <span
                        className={`badge-stats text-[10px] px-1.5 py-0.5 ${getRoleBadgeColor(employee.purchase_role)}`}
                      >
                        {getRoleDisplayName(employee.purchase_role)}
                      </span>
                    </TableCell>
                    <TableCell className="px-2 py-1.5">
                      <span
                        className={`badge-stats text-[10px] px-1.5 py-0.5 ${employee.is_active ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-600'}`}
                      >
                        {employee.is_active ? '' : ''}
                      </span>
                    </TableCell>
                    <TableCell className="px-1 py-1.5">
                      <DropdownMenu>
                        <DropdownMenuTrigger asChild>
                          <Button
                            variant="ghost"
                            className="h-7 w-7 p-0"
                            disabled={loadingId === employee.id}
                          >
                            <MoreHorizontal className="h-3 w-3" />
                          </Button>
                        </DropdownMenuTrigger>
                        <DropdownMenuContent align="end">
                          <DropdownMenuItem onClick={() => onView(employee)}>
                            <Eye className="mr-2 h-4 w-4" />
                             
                          </DropdownMenuItem>
                          <DropdownMenuItem onClick={() => onEdit(employee)}>
                            <Edit className="mr-2 h-4 w-4" />
                            
                          </DropdownMenuItem>
                          <DropdownMenuItem onClick={() => handleToggleStatus(employee)}>
                            {employee.is_active ? (
                              <>
                                <ToggleLeft className="mr-2 h-4 w-4" />
                                
                              </>
                            ) : (
                              <>
                                <ToggleRight className="mr-2 h-4 w-4" />
                                
                              </>
                            )}
                          </DropdownMenuItem>
                          <DropdownMenuItem 
                            onClick={() => handleDelete(employee)}
                            className="text-red-600"
                          >
                            <Trash2 className="mr-2 h-4 w-4" />
                            
                          </DropdownMenuItem>
                        </DropdownMenuContent>
                      </DropdownMenu>
                    </TableCell>
                  </>
                )}
              </TableRow>
            ))
          )}
        </TableBody>
          </Table>
        </div>
      </div>

      {/* Tablet View */}
      <div className="hidden md:block lg:hidden">
        <div className="overflow-x-auto border rounded-lg">
          <table className={`w-full ${canViewAllColumns ? 'min-w-[700px]' : 'min-w-[520px]'}`}>
            <thead className="bg-gray-50">
              <tr>
                <th className="text-left p-3 header-title text-gray-900 w-16"></th>
                <th className="text-left p-3 header-title text-gray-900 w-20"></th>
                <th className="text-left p-3 header-title text-gray-900 w-20"></th>
                <th className="text-left p-3 header-title text-gray-900 w-24"></th>
                <th className="text-left p-3 header-title text-gray-900"></th>
                <th className="text-left p-3 header-title text-gray-900"></th>
                {canViewAllColumns && (
                  <>
                    <th className="text-left p-3 header-title text-gray-900 w-24"></th>
                    <th className="text-center p-3 header-title text-gray-900 w-16"></th>
                    <th className="text-center p-3 header-title text-gray-900 w-16"></th>
                  </>
                )}
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {sortedData.length === 0 ? (
                <tr>
                  <td colSpan={canViewAllColumns ? 9 : 6} className="text-center py-8 text-gray-500">
                      .
                  </td>
                </tr>
              ) : (
                sortedData.map((employee) => (
                  <tr key={employee.id} className="hover:bg-gray-50">
                    <td className="p-3 modal-subtitle">
                      {employee.employeeID || employee.employee_number || employee.id.slice(0, 8)}
                    </td>
                    <td className="p-3 modal-value">{employee.name}</td>
                    <td className="p-3 modal-subtitle">{employee.position || '-'}</td>
                    <td className="p-3 modal-subtitle">{employee.department || '-'}</td>
                    <td className="p-3 modal-subtitle">{employee.phone || '-'}</td>
                    <td className="p-3 modal-subtitle">{employee.email || '-'}</td>
                    {canViewAllColumns && (
                      <>
                        <td className="p-3">
                          <span className={`badge-stats text-xs ${getRoleBadgeColor(employee.purchase_role)}`}>
                            {getRoleDisplayName(employee.purchase_role)}
                          </span>
                        </td>
                        <td className="p-3 text-center">
                          <span
                            className={`badge-stats text-xs ${employee.is_active ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-600'}`}
                          >
                            {employee.is_active ? '' : ''}
                          </span>
                        </td>
                        <td className="p-3 text-center">
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="sm" className="h-8 w-8 p-0">
                                <MoreHorizontal className="h-4 w-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem onClick={() => onView(employee)}>
                                <Eye className="mr-2 h-4 w-4" />
                                 
                              </DropdownMenuItem>
                              <DropdownMenuItem onClick={() => onEdit(employee)}>
                                <Edit className="mr-2 h-4 w-4" />
                                
                              </DropdownMenuItem>
                              <DropdownMenuItem onClick={() => handleToggleStatus(employee)}>
                                {employee.is_active ? (
                                  <>
                                    <ToggleLeft className="mr-2 h-4 w-4" />
                                    
                                  </>
                                ) : (
                                  <>
                                    <ToggleRight className="mr-2 h-4 w-4" />
                                    
                                  </>
                                )}
                              </DropdownMenuItem>
                              <DropdownMenuItem 
                                onClick={() => handleDelete(employee)}
                                className="text-red-600"
                              >
                                <Trash2 className="mr-2 h-4 w-4" />
                                
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </td>
                      </>
                    )}
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Mobile Card View */}
      <div className="block md:hidden space-y-3">
        {sortedData.length === 0 ? (
          <div className="text-center py-8 text-gray-500">
              .
          </div>
        ) : (
          sortedData.map((employee) => (
            <MobileCard key={employee.id}>
              <MobileCardHeader>
                <div className="flex justify-between items-center">
                  <span>{employee.name}</span>
                  {canViewAllColumns && (
                    <span
                      className={`badge-stats ${employee.is_active ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-600'}`}
                    >
                      {employee.is_active ? '' : ''}
                    </span>
                  )}
                </div>
              </MobileCardHeader>
              
              <MobileCardItem label="" value={employee.employeeID || employee.employee_number || employee.id.slice(0, 8)} />
              <MobileCardItem label="" value={employee.position || '-'} />
              <MobileCardItem label="" value={employee.department || '-'} />
              <MobileCardItem label="" value={employee.phone || '-'} />
              <MobileCardItem label="" value={employee.email || '-'} />
              {canViewAllColumns && (
                <>
                  <MobileCardItem 
                    label="" 
                    value={
                      <span className={`badge-stats ${getRoleBadgeColor(employee.purchase_role)}`}>
                        {getRoleDisplayName(employee.purchase_role)}
                      </span>
                    } 
                  />
                  <MobileCardItem label="" value={employee.adress || '-'} />
                  <MobileCardItem label="" value={employee.bank || '-'} />
                  <MobileCardItem label="" value={employee.bank_account || '-'} />
                  <MobileCardItem 
                    label="" 
                    value={employee.remaining_annual_leave !== undefined ? `${employee.remaining_annual_leave}` : '-'} 
                  />
                  
                  <MobileCardActions>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => onView(employee)}
                    >
                      <Eye className="w-4 h-4" />
                    </Button>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => onEdit(employee)}
                    >
                      <Edit className="w-4 h-4" />
                    </Button>
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => handleToggleStatus(employee)}
                      disabled={loadingId === employee.id}
                    >
                      {employee.is_active ? (
                        <ToggleLeft className="w-4 h-4" />
                      ) : (
                        <ToggleRight className="w-4 h-4" />
                      )}
                    </Button>
                    <Button
                      size="sm"
                      variant="outline"
                      className="text-red-600"
                      onClick={() => handleDelete(employee)}
                      disabled={loadingId === employee.id}
                    >
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </MobileCardActions>
                </>
              )}
            </MobileCard>
          ))
        )}
      </div>
    </>
  )
}